import React, { useState, useEffect } from 'react';
import { withStyles, useTheme } from '@material-ui/styles';
import { useDispatch, useSelector } from 'react-redux';
import useStyles from './styles';
import {
    Grid,
    IconButton,
    Typography,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
    Tooltip
} from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import { IconSearch } from '@tabler/icons';
import EditIcon from '@material-ui/icons/Edit';
import SubjectIcon from '@material-ui/icons/Subject';
import MainCard from '../../../ui-component/cards/MainCard';
import AddBookCategory from './add-category';
import { getAllCategory, getAllParentCategory } from '../../../store/actions/BookCategoryActions';
import useUserRole from '../../../hooks/useUserRole';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

export default function BookCategories() {
    const classes = useStyles();
    useUserRole({ roleList: ['ADMIN', 'SUPER_ADMIN'] });
    const dispatch = useDispatch();
    const theme = useTheme();
    const [value, setValue] = useState('');
    const [openAddSubCategory, setOpenAddSubCategory] = useState(false);
    const [isCategoryUpdateData, setUpdateCategoryData] = useState(null);
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(25);
    const [isSearch, setSearch] = useState('');
    const { allSubCategories } = useSelector((state) => state?.category);

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
    };
    const handleAddSubCategory = (id) => {
        setOpenAddSubCategory(true);
        setUpdateCategoryData(id);
    };

    const handleSearch = (event) => {
        setSearch(event?.target?.value);
    };

    useEffect(() => {
        dispatch(getAllCategory(rowsPerPage, isSearch ? isSearch : '', page + 1));
        dispatch(getAllParentCategory());
    }, [rowsPerPage, isSearch, page]);

    return (
        <>
            <MainCard>
                <div>
                    <div className={classes.titleContainer}>
                        <SubjectIcon className={classes.viewIcon} />
                        <span className={classes.pageTitle}>&nbsp; All Sub Categories </span>
                    </div>
                </div>
                <Paper elevation={5} className={classes.root}>
                    <Grid container spacing={3} style={{ display: 'flex', justifyContent: 'space-between' }}>
                        <Grid item xs={12} sm={6} md={6}>
                            <div>
                                <OutlinedInput
                                    className={classes.searchControl}
                                    id="input-search-header"
                                    onChange={(event) => handleSearch(event)}
                                    placeholder="Search"
                                    startAdornment={
                                        <InputAdornment position="start">
                                            <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                        </InputAdornment>
                                    }
                                />
                            </div>
                        </Grid>
                        <Grid item xs={12} sm={6} md={6}>
                            <div className={classes.btn}>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    onClick={() => {
                                        setOpenAddSubCategory(true);
                                        setUpdateCategoryData(null);
                                    }}
                                    className={classes.btnAddSubCategories}
                                >
                                    <AddIcon /> New
                                </Button>
                            </div>
                        </Grid>
                    </Grid>
                    <TableContainer className={classes.tableContainer}>
                        <Table className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHeaderCell}>
                                <StyledTableRow>
                                    <TableCell> Title </TableCell>
                                    <TableCell> Description </TableCell>
                                    <TableCell > Parent Category </TableCell>
                                    <TableCell align="center"> Status </TableCell>
                                    <TableCell align="right"> Action </TableCell>
                                </StyledTableRow>
                            </TableHead>
                            {!allSubCategories ? (
                                <caption style={{ textAlign: 'center' }}> Sorry! No data available </caption>
                            ) : (
                                <TableBody>
                                    {allSubCategories?.content.map((row) => (
                                        <StyledTableRow key={row?.id}>
                                            <TableCell>{row?.title}</TableCell>
                                            <TableCell>{row?.description}</TableCell>
                                            <TableCell>{row?.parentCategory}</TableCell>
                                            <TableCell align="center">
                                                <Typography
                                                    className={classes.status}
                                                    style={{
                                                        backgroundColor:
                                                            (row?.status === 'ACTIVE' && theme.palette.success.main) ||
                                                            (row?.status === 'PENDING' && 'blue') ||
                                                            (row?.status === 'INACTIVE' && theme.palette.warning.dark)
                                                    }}
                                                >
                                                    {row?.status}
                                                </Typography>
                                            </TableCell>
                                            <TableCell align="right">
                                                <Tooltip title="Update Category">
                                                    <IconButton onClick={() => handleAddSubCategory(row)}>
                                                        <EditIcon color="primary" style={{ cursor: 'pointer' }} />
                                                    </IconButton>
                                                </Tooltip>
                                            </TableCell>
                                        </StyledTableRow>
                                    ))}
                                </TableBody>
                            )}
                        </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[25, 50, 100]}
                        component="div"
                        count={allSubCategories?.totalElements || 0}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        sx={{
                            '.MuiTablePagination-displayedRows': {
                                marginBottom: 0
                            },
                            '.MuiTablePagination-selectLabel': {
                                marginBottom: 0
                            }
                        }}
                    />
                </Paper>
                {openAddSubCategory && (
                    <AddBookCategory
                        openAddSubCategory={openAddSubCategory}
                        setOpenAddSubCategory={setOpenAddSubCategory}
                        isEditData={isCategoryUpdateData}
                    />
                )}
            </MainCard>
        </>
    );
}
